import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            tab: ['从不', '偶尔', '经常', '每天'],
            activeTab: 0,
            dayNum: 1,
            age: 1,
            patient: this.$store.state.patient
        }
    },
    created(){
        const info = this.$store.state.Health
        if(info){
            this.activeTab = info.DrinkingHabit.DrinkingHabit - 1
            if(this.activeTab == 3){
                this.dayNum = info.DrinkingHabit.DrinkingNum
                this.age = info.DrinkingHabit.DrinkingAge
            }
        }
    },
    methods:{
        operate(i,t){
            if(t == 'del'){
                this[i] -= 1
            }
            else{
                this[i] += 1
            }
            if(this[i] < 0){
                this[i] = 0
            }
        },
        send(){
            const DrinkingNum = this.activeTab == 0 ? 0 : this.dayNum
            const DrinkingAge = this.activeTab == 0 ? 0 : this.age
            this.$post('PlatFormAPI/PatientAccount/SaveDrinkingHabit',{
                PatientId: this.patient.PatientId,
                DrinkingHabit: this.activeTab + 1,
                DrinkingNum,
                DrinkingAge,
            }).then(o => {
            	this.$store.dispatch('pushIsFresh',true)
                this.$toast('保存成功')
                this.$router.back()
            })
        }
    }
}



